
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'


export default {
name: "msg",
components: {
ButtonTab,
XButton,
ButtonTabItem,
Group,
Cell,
Swipeout,
SwipeoutItem,
SwipeoutButton
},
data: function() {
return {
selectedTabIndex: 0,
hasReadMsgs: [],
hasNotReadMsgs: []
};
},


created: function() {

var app = this;
this.$http
.get("/messages", {
params: {
accesstoken: this.$store.state.accesstoken
}
})
.then(function(response) {
if (response.data.success) {

app.hasReadMsgs = response.data.data.has_read_messages;
app.hasNotReadMsgs = response.data.data.hasnot_read_messages;
}
});
},
methods: {

markRead: function(msgid) {

this.$http
.post("/message/mark_one/" + msgid, {
accesstoken: this.$store.state.accesstoken
})
.then(function(response) {

console.log("已经标记为已读");


});
},

markAll: function() {
var app = this;
this.$http
.post("/message/mark_all", {
accesstoken: this.$store.state.accesstoken
})
.then(function(response) {
if (response.data.success) {
console.log("已经全部标记为已读");

app.$router.go(0);
}
});
}
}
};



