
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import { mapState } from "vuex";
export default {
name: "topiclist",
props: ["name"],
computed: {
...mapState(["topics"])
},
components: {
Tab,
TabItem,
Group,
Cell,
Scroller
},
data: function() {
return {
lists: [],
page: 1, //当前页数
limit: 10, //一页显示五条数据


pullupConfig: {
content: "上拉加载更多",
downContent: "松开进行加载",
upContent: "上拉加载更多",
loadingContent: "加载中..."
},
pulldownConfig: {
content: "下拉刷新",
downContent: "松开刷新数据",
upContent: "下拉刷新数据",
loadingContent: "加载中..."
}
};
},
methods: {

getDescByTab(tab) {
return this.$store.getters.getTopicDescByTab(tab);
},

routeto(index) {

console.log("index=" + index);

this.$router.push("/topiclist/" + this.topics[index].name);
},

loadMoreData: function() {
var app = this;

this.$http
.get("/topics", {
params: {
tab: this.name,
page: this.page + 1,
limit: this.limit
}
})
.then(function(response) {




for (var i = 0; i < response.data.data.length; i++) {
app.lists.push(response.data.data[i]);
}
});


app.$refs.pullup.donePullup();
},

freshData: function() {
var app = this;
this.$http
.get("/topics", {
params: {
tab: this.name,
page: 1,
limit: this.limit
}
})
.then(function(response) {
if (response.data.success) {
app.lists = response.data.data;


app.$refs.pullup.donePulldown();
}
});
}
},



beforeRouteEnter: function(to, from, next) {




next(function(vm) {

vm.$http
.get("/topics", {
params: {
tab: to.params.name,
page: vm.page,
limit: vm.limit
}
})
.then(function(response) {



if (response.data.success) {
vm.lists = response.data.data;
}
});
});
},

beforeRouteUpdate: function(to, from, next) {

var app = this;
this.$http
.get("/topics", {
params: {
tab: to.params.name,
page: this.page,
limit: this.limit
}
})
.then(function(response) {



if (response.data.success) {

app.lists = response.data.data;
}
});
}
};



