
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Card from 'vux/src/components/card/index.vue'

export default {
name:'collection',
props:['loginname'],
components:{
Group,
Cell,
Card
},
data:function(){
return {
collect_topics:''
}
},
beforeRouteEnter:function(to,from,next){
next(function(vm){

vm.$http.get('/topic_collect/'+to.params.loginname).then(function(response){
console.log(response.data);
vm.collect_topics=response.data.data;
})
})
},
beforeRouteUpdate:function(to,from,next){
var app=this;

this.$http.get('/topic_collect/'+to.params.loginname).then(function(response){

app.collect_topics=response.data.data;
})
next();
}
}



