export default {
    name:'center',
    computed: {
        loginname :function(){
            //直接获取登录名
            return this.$store.state.loginname;
        }
    },
    created:function(){
        //跳转到用户界面,使用编程式路由跳转
        //push：会向history添加一个记录,点击后退返回到上一个界面
        //replace：不会向history添加记录,返回上上个界面,即上一个页面的记录不存在[代替]
        this.$router.replace('/user/'+this.loginname);
    }
}



